<script setup>
import $ from 'jquery'
import {useUserStore} from "../../store/useUserStore";

$(document).ready(function () {
  // 给所有含有子菜单的li元素添加一个类名
  $('.el-submenu').addClass('has-children');

  // 给所有含有子菜单的li元素添加一个点击事件
  $('.has-children > .el-submenu__title').click(function () {
    // 获取当前点击的li元素
    const $li = $(this).parent();

    // 判断当前li元素是否已经展开
    if ($li.hasClass('is-opened')) {
      // 如果已经展开，则收起子菜单
      $li.removeClass('is-opened');
      $li.find('.el-menu').hide();
    } else {
      // 如果未展开，则展开子菜单
      $li.addClass('is-opened');
      $li.find('.el-menu').show();

      // 隐藏其他子菜单
      $li.siblings().removeClass('is-opened');
      $li.siblings().find('.el-menu').hide();
    }
  });
});
</script>

