</template>

<script setup>
import {ref} from "vue";
import {useRoute} from "vue-router";
import axios from "@/api/axios";

const goodList = ref([])
const route = useRoute();
axios.get('/goodsku/sku/',{
  params:{
    size:20,
    category:route.params.id,
  }
}).then(res => {
  // console.log(res.data)
  goodList.value = res.data
})

