<template>
  <div class="list list-two">
    <div class="list-button">
      <el-checkbox :model-value="choose"
                   @update:modelValue="$emit('update:choose',$event)"/>
    </div>
    <div class="list-img">
      <img :src="item.products.good_img">
    </div>
    <div class="list-num-text">
      <router-link :to="'goodsku/xid/' +item.products.id">{{ item.products.good_info }}</router-link>
    </div>
    <div class="list-price">
      <p>￥{{ item.products.good_new_price }}</p>
    </div>
    <div class="list-number">
      <div class="list-number-num">
        <el-input-number :min="1" style="width: 80px" size="small" v-model="count"/>
      </div>
    </div>
    <div class="list-sum">
      <p>￥{{ item.products.good_new_price * count }}</p>
    </div>
    <div class="list-delete" @click="del">
      <p>删除</p>
    </div>
  </div>
</template>

<script setup>
import {computed, ref, watch, watchEffect} from "vue";
import axios from "@/api/axios";

const count = ref(1)
const props = defineProps(['item', 'choose'])
const emit = defineEmits(["update:choose", "update:price" ,"delete"])


const value = computed(() => {
  return props.item.products.good_new_price * count.value
})
watchEffect(() => {
  if (props.choose) {
    emit('update:price', value.value)
  } else {
    emit('update:price', 0)
  }
})
watch(value, (newValue) => {
  if (props.choose) {
    emit('update:price', newValue)
  } else {
    emit('update:price', 0)
  }
})


function del(){
  axios.post('/shop/del/'+props.item.id,{
  }).then(res => {
    emit("delete")
  })
}
</script>

<style scoped>

.list-num-text {
  width: 580px;
  height: 80px;
}

.list-img {
  margin: 10px 8px 10px 20px;
}

.list-number-num {
  width: 50px;
}

.list-two {
  height: 80px;
  line-height: 80px;
  padding-left: 15px;
  margin-bottom: 15px;
}

.list-num-text a {
  text-decoration: none;
  color: black;
  height: 80px;
  line-height: 80px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}

.list-num-text a:hover {
  color: #ff547b;
}

.list-img img {
  width: 60px;
  height: 60px;
  overflow: hidden;
}

.list-price {
  width: 115px;
}

.list-number {
  width: 128px;
}

.list-sum {
  width: 130px;
}

.list-sum p {
  color: #ff3d69;
}
</style>