<template>
  <div class="annex-all">
    <div class="annex-li">
      <span class="iconfont icon-gouwuche"></span>
      <router-link to="/shopcarView">购物车</router-link>
    </div>
    <div class="annex-li">
      <span class="iconfont icon-erweima"></span>
      <a href="">手机购买</a>
    </div>
    <div class="annex-li">
      <span class="iconfont icon-lianxikefu"></span>
      <a href="">联系客服</a>
    </div>
    <router-link to="/indiView">
    <div class="annex-li">
      <span class="iconfont icon-kefu"></span>
      <p>会员中心</p>
    </div>
    </router-link>
  </div>
  <div class="annex-top" @click="goTop" v-show="btnFlag">
    <div class="annex-li">
      <span class="iconfont icon-top"></span>
      <a href="">回到顶部</a>
    </div>
  </div>
</template>

<script>


export default {
  name: "Annex",
  data() {
    return{
      btnFlag:false
    }
  },
  mounted(){
    window.addEventListener("scroll",this.scrollToTop)
  },
  destroyed(){
    window.removeEventListener("scroll",this.scrollToTop)
  },

  methods: {
    goTop() {
      const that = this;
      let timer = setInterval(()=>
        {
          let ispeed = Math.floor(-that.scrollTop / 5)
          document.documentElement.scrollTop = document.body.scrollTop = that.scrollTop + ispeed
          if (that.scrollTop === 0) {
            clearInterval(timer)
          }
        },16)
      },

    scrollToTop(){
      const that = this;
      let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
      that.scrollTop = scrollTop
      if (that.scrollTop > 60) {
        that.btnFlag = true
      }else {
        that.btnFlag = false
      }
    }
  }

}
</script>

<style scoped>
a {
  text-decoration: none;
  font-size: 12px;
  color: #383838;
}
.annex-all {
  width:88px;
  height: 350px;
  background-color: #fff;
  border-radius: 5px;
  position: fixed;
  top: 200px;
  right: 10px;
  z-index: 9999;
  box-shadow: 0 2px 12px 0 rgba(102,128,153,.16);
}
.annex-top {
  width: 88px;
  height: 85px;
  background-color: #fff;
  border-radius: 5px;
  position: fixed;
  top:  570px;
  right: 10px;
  z-index: 9999;
  box-shadow: 0 2px 12px 0 rgba(102,128,153,.16);
}

.annex-li {
  width: 88px;
  height: 85px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.annex-li:after {
  content: "";
  transform: translateY(18px);
  width: 60px;
  height: 1px;
  background: #ececec;
}
.annex-li:last-child:after {
  content: "";
  transform: translateY(0px);
  width: 60px;
  height: 0;
  background: #ececec;
}
.annex-li span{
  margin-bottom: 10px;
}
.iconfont {
  font-size: 23px;
}
</style>