<template>
  <div class="have-shop-car">
    <div class="list-group">
      <div class="list list-one">
        <div class="list-button">
          <el-checkbox v-model="allChooseStatus"/>
        </div>
        <div class="list-text">
          <p>商品信息</p>
        </div>
        <div class="list-price">
          <p>价格</p>
        </div>
        <div class="list-number">
          <p>数量</p>
        </div>
        <div class="list-sum">
          <p>小计</p>
        </div>
        <div class="list-oper">
          <p>操作</p>
        </div>
      </div>
      <div  v-for="(item,index) in list" :key="item.id">
        <ShopItem :item="item"  v-model:choose="chooseList[index]" @update:price="allPrice[index]=$event" @delete="$emit('delete')"/>

      </div>
      <div class="list list-three">
        <div class="list-button">
          <el-checkbox v-model="allChooseStatus"/>
        </div>
        <div class="list-span-text">
          <p>全选</p>
          <p>删除</p>
        </div>
        <div class="list-count">
          <p>已选商品 {{count}} 件</p>
          <p>合计(不含运费):￥{{ price }}</p>
          <button>结算（{{ count }}）</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import {reactive, ref, watch, watchEffect} from 'vue'
import ShopItem from "@/components/shopping/ShopItem.vue";

const props = defineProps(['list'])
const chooseList = reactive([])
const allPrice = reactive([])

const emit = defineEmits(['delete'])

let price = ref(0)
watch(allPrice,()=>{
  let sum =0
  allPrice.forEach((price,key)=>{
    sum += price
  })
  price.value = sum
})

const count = ref(0)
// 初始化数组，把按钮默认为false
props.list.forEach((item,key)=>{
  chooseList[key] = false
})

const allChooseStatus = ref(false)
watch(allChooseStatus,()=>{
  count.value = 0
  chooseList.forEach((item, index) => {
    if(item){
      count.value ++
    }
  })
  if(count.value === chooseList.length){
    if(allChooseStatus.value === false) {
      chooseList.forEach((item,index)=>{
        chooseList[index] = false
      })
    }
  }else{
    if(allChooseStatus.value === true) {
      chooseList.forEach((item,index)=>{
        chooseList[index] = true
      })
    }
  }
})

watchEffect(()=>{
  count.value = 0
  chooseList.forEach((item, index) => {
      if(item){
        count.value ++
      }
  })
  if(count.value === chooseList.length){
    allChooseStatus.value = true
  }else{
    allChooseStatus.value = false
  }
})
</script>

<style scoped>
  .have-shop-car{
    width:1200px;
    margin: 0 auto;
  }

  .list{
    display: flex;
    padding-left: 15px;
    background-color: white;
  }
  .list-one{
    height:45px;
    line-height: 45px;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  .list-button{
    margin-right: 15px;
    width: 25px;
  }
  .list-button input{
    zoom: 110%;
  }
  .list-text{
    width: 650px;
  }
  .list-price{
    width:115px;

  }
  .list-number{
    width:128px;
  }
  .list-sum{
    width: 119px;
  }
  .list-sum p{
    color: #ff3d69;
  }
  .list-oper{
    width:83px;
  }

  .list-three{
    height: 60px;
    line-height: 60px;
    padding-left: 15px;
    margin-bottom: 15px;
  }
  .list-span-text{
    display:flex;
    width: 650px;
  }
  .list-span-text p{
    margin-left: 8px;
    margin-right: 8px;
  }
  .list-span-text p:nth-child(2):hover{
    color:#ff547b;
  }
  .list-count{
    display:flex;
  }
  .list-count p:nth-child(2){
    width: 210px;
    padding-left: 30px;
    /*padding-right: 50px;*/
  }
  .list-count button{
    height:36px;
    padding: 8px 18px;
    background-color: #ff547b;
    color: white;
    border-radius: 15px;
    margin-top: 12px;
  }
</style>