<template>

  <div class="card" v-for="cos in goodList" :key="cos.id">
    <div class="card-img">
      <img :src="cos.good_img">
    </div>
    <router-link :to="'/goodsku/xid/'+cos.id">
    <div class="card-text">
      <p>{{ cos.good_info }}</p>
    </div>
    <div class="card-price">
      <p class="new-price">{{ cos.good_new_price }}元 </p><p class="old-price"> {{ cos.good_old_price }}元</p>
    </div>
    </router-link>
  </div>

</template>

<script setup>
import {ref} from "vue";
import {useRoute} from "vue-router";
import axios from "@/api/axios";

const goodList = ref([])
const route = useRoute();
axios.get('/goodsku/sku/',{
  params:{
    size:10,
    category:23,
  }
}).then(res => {
  goodList.value = res.data
})
</script>

