import {ref} from "vue";
// import {useRoute} from "vue-router";
import axios from "@/api/axios";

const list = ref([])

function load(){
  // const route = useRoute();
  axios.get('/shop/dellist',{
  }).then(res => {
    list.value = res.data
  })
}

load()
</script>

