<template>
  <div class="deputy-first">
    <div class="deputy-left">
      <p><span class="iconfont icon-shoucang"></span> 收藏本站</p>
      <div class="deputy-right">
        <router-link to="/login" class="login-rig" v-if="!useUserStore().user">登录 | 注册 </router-link>
        <router-link to="/indiView" class="login-rig" v-else> {{ useUserStore().user.username }} &nbsp;</router-link>
        <span class="iconfont icon-lingdang-xianxing" style="color: #c2c2c2"></span>
        <a href="" class="my-orders"> &nbsp;我的订单 </a>
        <div class="login-rig" v-if="useUserStore().user" @click="useUserStore().userLogout()"> &nbsp;退出登录 </div>
      </div>

      <div class="clear"></div>
    </div>
  </div>
  <div class="fixed" :class=" fixed ? isFixed:''">
    <div class="navigation-bar-all">
      <div class="navigation-bar-left">
        <ul>
          <li><router-link to="/" active-class="active">首页</router-link></li>
          <li><router-link to="/proall">所有产品</router-link></li>
          <li><a href="">领券中心</a></li>
          <li><a href="">秒杀专区</a></li>
          <li><a href="">团购专区</a></li>
        </ul>
      </div>
      <div class="navigation-bar-right">
        <el-input
            class="w-50"
            size="small"
            placeholder="小米"
            :prefix-icon="Search"
        />
      </div>
      <div class="clear"></div>
    </div>
    <div class="clear"></div>
  </div>

</template>

<script >
import {Search} from '@element-plus/icons-vue'
import {useUserStore} from "@/store/useUserStore";

export default {
  name: 'Deputy',
  computed: {
    Search() {
      return Search
    }
  },

  data() {
    return {
      fixed: ''
    }
  },
  methods: {
    useUserStore,
    mounted() {
      window.addEventListener('scroll', this.handleScroll);
    },
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll);
    },
    handleScorll() {
      let scrollTop =
          window.pageYOffset ||
          document.documentElement.scrollTop ||
          document.body.scrollTop;

      if (scrollTop > 45) {
        this.fixed = true
      } else {
        this.fixed = false
      }
    },
  }

}

</script>


<style scoped lang="css">
a {
  text-decoration: none;
}
.deputy-first {
  background-color: #1f1f1f;
  width: 100%;
  height: 45px;
  position: relative;
}

.deputy-left {
  width: 1200px;
  height: 45px;
  margin: 0 auto;
}

p {
  color: #dedede;
  width: 90px;
  font-size: 14px;
  line-height: 45px;
  position: absolute;
}

.deputy-right {
  height: 45px;
  line-height: 45px;
  float: right;
  display: flex;
}
.login-rig{
  color:#c2c2c2;
  font-size: 14px;
}
.deputy-right a {
  color: #c2c2c2;
  font-size: 14px;
  line-height: 45px;
}
.deputy-right a:hover, .login-rig:hover{
  color: #ff547b;
}

.fixed {
  background-color: #ffffff;
  width: 100%;
  height: 100px;
  line-height: 100px;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navigation-bar-all {
  width: 1200px;
  margin: 0 auto;
}

.navigation-bar-left {
  width: 400px;
  position: absolute;
}


.navigation-bar-left ul li {
  display: inline;
  margin-left: 10px;
}

.navigation-bar-left ul li a {
  color: #000000;
  font-size: 16px;
}
.navigation-bar-left ul li .active {
  color:#ff547b;
}
.navigation-bar-left ul li a:hover{
  color:#ff547b;
}

.w-50 {
  width: 300px;
  height: 35px;
}

.navigation-bar-right {
  float: right;
}
.deputy-left p:hover,.my-orders:hover {
  color: #ff547b;
}
</style>