import {createRouter, createWebHistory, RouteRecordRaw} from 'vue-router'
import HomeView from '../views/HomeView.vue'
import LoginRig from "@/views/LoginRig.vue";
import Index from "@/views/home/Index.vue";
import HouseholdAppliances from "@/views/CategoryList/HouseholdAppliances.vue";
import DetailView from "@/views/CategoryList/DetailView.vue";
import IndiView from "@/views/CategoryList/IndiView.vue";
import ProAll from "@/views/CategoryList/ProAll.vue";
import ShopCarView from "@/views/CategoryList/ShopCarView.vue";

const routes: Array<RouteRecordRaw> = [
    {
        path: '/',
        name: 'home',
        component: HomeView,
        children: [
            {
              path: '/',
              name: 'index',
              component: Index,
            },
            {
                path:'/goodsku/xid/:id',
                name:'detailView',
                component:DetailView,
            },
            {
                path:'/proall',
                name:'ProAll',
                component:ProAll,
            },
        ]

    },
    {
        path:'/category/:id',
        name:'household',
        component: HouseholdAppliances
    },
    {
        path: '/indiView',
        name: 'indiView',
        component: IndiView,
    },
    {
        path: '/login',
        name: 'LoginRig',
        component: LoginRig
    },
    {
      path: '/shopCarView',
      name:'shoppingcar',
      component: ShopCarView
    },

]

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes
})

export default router
